.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url("https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:wght@400;500;700;900&display=swap");

*,
html,
body {
  margin: 0;
  padding: 0;
  font-family: "Be Vietnam Pro", sans-serif;
  position: relative;
}

.fcontainer {
  display: grid;
  width: 100%;
  margin: 0 auto;
  height: 100vh;
  background-color: white;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  padding: 15px;
  width: 100%;
  height: 25px; /* Sesuaikan tinggi header sesuai kebutuhan */
  background-color: #ffffff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Definisikan bayangan dengan offset-x, offset-y, blur-radius, dan warna */
}

.brand {
  display: flex;
  flex-direction: row;
  font-size: 1.5rem;
  padding: 100px;
  text-transform: capitalize;
}

.firstname {
  color: #222222;
  font-weight: 800;
}

.lastname {
  color: #898989;
  font-weight: 600;
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px;
}

.navigation > li {
  text-transform: capitalize;
  list-style-type: none;
  padding: 15px;
}

.navigation > li > a {
  color: #222222;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
}

#hero {
  text-align: center;
  height: 750px;
  display: center;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.quote {
  text-align: center;
}

.subkata p {
  text-align: center;
  font-family: "Be Vietnam Pro", sans-serif;
  font-size: 55px;
  font-weight: bold;
  margin-bottom: -50px; /* Mengubah jarak antara teks dengan elemen di bawahnya */
  margin-top: 50px; /* Menambahkan jarak di atas teks */
}

.subkata {
  margin-bottom: 15px; /* Menambahkan jarak bawah antara subkata dengan input-section */
}

.input-section {
  margin-top: 30px; /* Menambahkan jarak atas antara input-section dengan subkata */
}

.container {
  width: 536px;
  height: 600px;
  margin: auto;
  padding: 20px;
  position: relative; /* Tambahkan properti posisi relatif */
}

.input-section,
.answer-section {
  width: 100%;
  margin-bottom: 10px;
  position: relative; /* Tambahkan properti posisi relatif */
}

.question-input {
  width: calc(100% - 0px); /* Adjust the width to leave space for the button */
  height: 30px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  color: #222222;
  position: relative; /* Tambahkan properti posisi relatif */
  margin-right: 10px; /* Berikan jarak ke tombol */
}

.calculate-button {
  position: absolute; /* Tambahkan properti posisi absolut */
  top: 0; /* Letakkan ke atas */
  right: 0; /* Letakkan ke kanan */
  transform: translate(
    0%,
    35%
  ); /* Geser ke kiri sejauh 100% lebar input, dan turun 50% tinggi input */
  height: 30px;
  padding: 0 10px;
  border: none;
  border-radius: 4px;
  background-color: #222222;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}

.answer-section {
  height: 300px;
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  color: #222222;
}

.answer-header {
  width: auto;
  height: 20px;
  background-color: #eaeaea;
  padding: 10px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.answer-header h2 {
  color: #333;
  font-size: 20px;
}

/* Footer Section */
footer {
  padding: 4rem 0;
  background: #333;
  text-align: center;
  color: #f7f7f7;
}

.judul {
  font-size: 30px;
  margin-bottom: 10px;
  font-family: "Be Vietnam Pro", sans-serif;
  font-weight: bold;
}

.footer-icons {
  margin-bottom: 1rem;
}

.footer-icons a {
  margin: 0 0.5rem;
}

.footer-icons svg {
  fill: #ffffff;
}

.footer-icons a:hover svg {
  fill: #ffffff;
}

/* Photo Section */
.photo {
  display: flex;
  justify-content: space-between; /* Memastikan design1 dan design3 berada di sisi kiri dan kanan */
  align-items: flex-start; /* Mengatur posisi vertikal objek dalam .photo ke atas */
}

.design1,
.design2,
.design3 {
  flex-shrink: 0; /* Mencegah objek mengurangi ukuran ketika ruang terbatas */
}

.design1 {
  position: absolute; /* Mengatur posisi elemen */
  top: 0; /* Atur posisi dari atas */
  left: 50%; /* Atur posisi dari tengah */
  transform: translateX(70%) translateY(-470%); /* Geser elemen ke atas */
}

.design2 {
  order: 2; /* Mengatur urutan objek, ini memastikan design2 berada di sisi kiri */
  /* Mengatur jarak antara design2 dan desi n1 */
  align-self: flex-start; /* Mengatur posisi objek ke atas */
  transform: translateY(
    -50%
  ); /* Anda dapat menyesuaikan margin sesuai kebutuhan untuk menentukan posisi design2 */
}

.design3 {
  order: 3; /* Mengatur urutan objek, ini memastikan design3 berada di sisi kanan */
  /* Mengatur jarak antara design3 dan design1 */
  align-self: flex-start; /* Mengatur posisi objek ke atas */
  transform: translateY(
    -55%
  ); /* Anda dapat menyesuaikan margin sesuai kebutuhan untuk menentukan posisi design3 */
}
